@import "../../stylesheets/colors";
@import "../../stylesheets/mixins";
@import "../../stylesheets/constants";

.header {
	position: relative;
	z-index: 10;
	padding: 0 20px;
	background: $page-bg;

	@include media($header-menu-breakpoint1) {
		padding: 0;
	}
}

.header__inner {
	max-width: 1180px;
	height: $header-height;
	margin: 0 auto;
	-webkit-box-align: center;
	-ms-flex-align: center;
	@include media($header-menu-breakpoint2) {
		flex-direction: column;
		padding-right: 0;
		height: $header-height-mobile;
	}
}

.header__primary {
	height: 77px;
	display: flex;
	align-items: center;
	width: 100%;

	@include media($header-menu-breakpoint2) {
		width: 100%;
		height: 62px;
	}
}

.header-primary {
	background: $main-gray;
	position: relative;
	height: 100%;
	display: flex;
	align-items: center;
	justify-content: stretch;
	box-sizing: border-box;
	width: 100%;
	@include media($header-menu-breakpoint1) {
		padding-left: 0;
	}

	&:before {
		content: "";
		position: absolute;
		top: 0;
		bottom: 0;
		right: 0;
		z-index: -10;
		left: -10000px;
		background: $main-gray;
	}
	&:after {
		content: "";
		position: absolute;
		top: 0;
		bottom: 0;
		left: 0;
		z-index: -10;
		right: -10000px;
		background: $main-gray;
	}
}

.header__secondary {
	display: flex;
	flex-direction: column;
	justify-content: flex-end;
	box-sizing: border-box;
	width: 100%;

	height: calc(#{$header-height} - 77px);

	@include media($header-menu-breakpoint1) {
		padding: 0 20px;
	}

	@include media($header-menu-breakpoint2) {
		display: none;
	}
}

.header-secondary {
	width: 100%;
	height: 100%;
	box-sizing: border-box;
	background: $page_bg;
	display: flex;
	justify-content: space-between;
	&__section {
		display: flex;
		align-items: center;
		justify-content: space-between;
		&:first-child {
			margin-right: 24px;
			flex-grow: 1;
		}
	}
}

.header__logo {
	max-width: 250px;
	overflow: hidden;
	height: 100%;

	@include media($header-menu-breakpoint2) {
		margin: 0 auto;
	}
}

.logo,
.logo:visited {
	display: flex;
	align-items: center;
	line-height: 1;
	text-decoration: none;
	overflow: hidden;
	height: 100%;

	img {
		width: 100%;
		height: 100%;
		object-fit: contain;
		object-position: left;
		@include media($header-menu-breakpoint2) {
			object-position: center;
		}
	}
}

.nav-menu {
	&__list {
		display: flex;
		justify-content: flex-end;
		align-items: center;
		margin: 0;
		padding: 0;
	}
	&__item {
		position: relative;
		padding: 0;
		list-style: none;
	}

	&__link,
	&__link:visited {
		padding: 4px 20px;
		box-sizing: border-box;

		text-transform: uppercase;
		position: relative;
		font-weight: bold;
		min-width: 100px;
		text-align: center;
		display: block;
		max-width: 180px;
		overflow: hidden;
		font-size: 16px;
		color: whitesmoke;
		text-decoration: none;
		text-overflow: ellipsis;
		transition: all 280ms ease-in-out;

		&:hover {
			background: $mid-gray;
		}
	}
}

.mobile-menu-trigger {
	margin: 0 8px;
	display: none;
	@include media($header-menu-breakpoint1) {
		display: block;
	}
	&_btn {
		padding: 12px;
	}
}

@include keyframes(shine) {
	0% {
		background-position: 0;
	}
	60% {
		background-position: 180px;
	}
	100% {
		background-position: 240px;
	}
}
