@import "../../../../stylesheets/colors";
@import "../../../../stylesheets/mixins";
@import "../../../../stylesheets/constants";

.main-slider {
}

.main-slider-slide {
	position: relative;
	img {
		object-fit: cover;
		width: 100%;
		height: 318px;
		background-position: center;
		@include media($header-menu-breakpoint2) {
			height: 440px;
		}
	}

	&__overlay {
		z-index: 2;
		position: absolute;
		opacity: 0.4;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		background: $main-gray;
	}
	&__content {
		display: flex;
		flex-direction: column;
		height: 100%;
		color: white;
		padding: 40px;
		@include media($header-menu-breakpoint2) {
			padding: 16px;
			.main-slider-slide__title {
				font-size: em(24);
			}
			.main-slider-slide__text {
				font-size: em(16);
			}
		}

		&-inner {
			max-width: 400px;
		}
	}

	&__title {
		width: 100%;
		font-size: em(24);
		font-weight: bold;
		text-transform: uppercase;
		margin-bottom: 16px;
		@include media(380px) {
			font-size: em(32);
		}
	}
	&__text {
		width: 100%;
		margin: 0;
		font-size: 16px;
		line-height: 110%;
		@include media(380px) {
			font-size: em(18);
		}
	}
}

.main-slider__slide:nth-child(3) .main-slider-slide__content {
	align-items: flex-end;
	text-align: right;
}
