@import url("https://fonts.googleapis.com/css?family=Roboto:100,100i,300,300i,400,400i,500,500i,700,700i,900,900i&subset=cyrillic");
@import "constants";

audio,
canvas,
iframe,
img,
svg,
video {
	vertical-align: middle;
}

fieldset {
	margin: 0;
	padding: 0;

	border: none;
}

textarea {
	resize: vertical;
}

input[type="text"],
input[type="email"],
input[type="search"],
input[type="password"] {
	-webkit-appearance: none;
	-moz-appearance: none;
}

