html {
	font-size: 16px;
	//font-size: 62.5%;
}

body {
	//font-size: 1.6em;
	min-width: 320px;
	margin: 0;
	padding: 0;
	overflow-x: hidden;

	line-height: 1.6;
	font-family: "fira_sansregular", Arial, "Helvetica Neue", Helvetica, sans-serif;
	color: #282c37;

	background: #f5f7fa;
	-webkit-overflow-scrolling: touch;
	-webkit-font-smoothing: subpixel-antialiased;
	-moz-osx-font-smoothing: auto;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 1.6em 0 0.5em;

	font-weight: 500;
	line-height: normal;
	font-family: "fira_sansbold", Arial, "Helvetica Neue", Helvetica, sans-serif;
}

h1 a,
h2 a,
h3 a,
h4 a,
h5 a,
h6 a,
h1 a:visited,
h2 a:visited,
h3 a:visited,
h4 a:visited,
h5 a:visited,
h6 a:visited {
	color: inherit;
}

h1:first-child,
h2:first-child,
h3:first-child,
h4:first-child,
h5:first-child,
h6:first-child {
	margin-top: 0;
}

h1:last-child,
h2:last-child,
h3:last-child,
h4:last-child,
h5:last-child,
h6:last-child {
	margin-bottom: 0;
}

h1 {
	font-size: 2.25em;
}

h2 {
	font-size: 2em;
}

h3 {
	font-size: 1.75em;
}

h4 {
	font-size: 1.5em;
}

h5 {
	font-size: 1.25em;
}

h6 {
	font-size: 1em;
}

a {
	color: #2c87db;
	text-decoration: underline;

	-webkit-transition: 125ms color ease-out;
	-o-transition: 125ms color ease-out;
	transition: 125ms color ease-out;
}

a:visited {
	color: #6741d9;
}

a:hover {
	color: #951005;
	text-decoration: none;
}

p {
	margin-top: 1.6em;
	margin-bottom: 1.6em;
}

p:first-child {
	margin-top: 0;
}

p:last-child {
	margin-bottom: 0;
}

i,
em {
	font-style: italic;
}

b,
strong {
	font-weight: bold;
}

q {
	font-style: italic;

	quotes: "«" "»";
}

q q {
	font-style: italic;

	quotes: "„" "“";
}

mark,
kbd,
code {
	display: inline-block;
	padding: 0 4px;

	vertical-align: middle;

	border-radius: 3px;
}

kbd {
	color: #fff;

	background-color: #222;
}

hr {
	display: block;
	height: 1px;
	margin: 1em 0;
	padding: 0;

	border: 0;
	border-top: 1px solid #ccc;
}

table th,
table td {
	padding: 0 0.5em;
}

table tbody th {
	text-align: inherit;
}

ol,
ul {
	padding-left: 2em;
}

dt {
	font-weight: bold;
}

dd {
	margin-bottom: 1.6em;
	margin-left: 0;
	padding-left: 1.6em;
}

pre,
code {
	overflow: hidden;

	font-family: Consolas, "Andale Mono WT", "Andale Mono", "Lucida Console", "Lucida Sans Typewriter", "DejaVu Sans Mono",
		"Bitstream Vera Sans Mono", "Liberation Mono", "Nimbus Mono L", Monaco, "Courier New", Courier, monospace;
	white-space: pre-wrap;
	word-wrap: break-word;
	-moz-tab-size: 4;
	-o-tab-size: 4;
	tab-size: 4;

	background-color: #e9ecef;
}

pre {
	padding: 1em;

	border-radius: 3px;
}

blockquote {
	margin: 0 0 1.6em;
	padding: 0.8em;

	border-left: 5px solid #eee;
}

blockquote p:last-child,
blockquote ul:last-child,
blockquote ol:last-child {
	margin-bottom: 0;
}

blockquote p:first-child,
blockquote ul:first-child,
blockquote ol:first-child {
	margin-top: 0;
}

blockquote footer,
blockquote small {
	display: block;

	font-size: 80%;
	line-height: 1.6em;
}

blockquote footer:before,
blockquote small:before {
	content: "\2014 \00A0";
}

.clearfix:before,
.clearfix:after {
	content: "";

	display: table;
}

.clearfix:after {
	clear: both;
}

.content-wrapper:before,
.content-wrapper:after {
	content: "";

	display: table;
}

.content-wrapper:after {
	clear: both;
}

.content-wrapper > *:first-child {
	margin-top: 0 !important;
}

.content-wrapper > *:last-child {
	margin-bottom: 0 !important;
}

.disabled {
	cursor: default;
	opacity: 0.5;

	pointer-events: none;
}

.f-left {
	float: left;
}

.f-right {
	float: right;
}

.f-none {
	float: none;
}

.hidden {
	display: none !important;
}

.hyphenate {
	word-wrap: break-word;
	-ms-word-break: break-all;
	word-break: break-word;
	-ms-hyphens: auto;
	-webkit-hyphens: auto;
	hyphens: auto;
	overflow-wrap: break-word;
}

.invisible {
	visibility: hidden;
}

.ta-right {
	text-align: right;
}

.ta-center {
	text-align: center;
}

.ta-left {
	text-align: left;
}

.bold {
	font-weight: bold;
}

.italic {
	font-style: italic;
}

.uppercase {
	text-transform: uppercase;
}

.lowercase {
	text-transform: lowercase;
}

.capitalize {
	text-transform: capitalize;
}

.caps {
	font-variant: small-caps;
}

.visuallyhidden,
.visually-hidden,
.sr-only {
	position: absolute;

	width: 1px;
	height: 1px;
	padding: 0;
	overflow: hidden;

	white-space: nowrap;

	border: none;

	clip: rect(0, 0, 0, 0);
}

.visuallyhidden.focusable:active,
.visually-hidden.focusable:active,
.sr-only.focusable:active,
.visuallyhidden.focusable:focus,
.visually-hidden.focusable:focus,
.sr-only.focusable:focus {
	position: static;

	width: auto;
	height: auto;
	overflow: visible;

	white-space: normal;

	clip: auto;
}

@media only screen and (max-width: 480px) {
	ol,
	ul {
		padding-left: 0;

		list-style-position: inside;
	}
}

@media print {
	.print-hidden {
		display: none !important;
	}

	.print-invisible {
		visibility: hidden !important;
	}

	*,
	*:before,
	*:after {
		color: #000 !important;
		text-shadow: none !important;

		background: transparent !important;
		/* Black prints faster: http://www.sanbeiji.com/archives/953 */
		-webkit-box-shadow: none !important;
		box-shadow: none !important;
	}

	a,
	a:visited {
		text-decoration: underline;
	}

	a[href]:after {
		content: " (" attr(href) ")";
	}

	abbr[title]:after {
		content: " (" attr(title) ")";
	}

	a[href^="#"]:after,
	a[href^="javascript:"]:after {
		content: "";
	}

	pre {
		white-space: pre-wrap !important;
	}

	pre,
	blockquote {
		page-break-inside: avoid;

		border: 1px solid #999;
	}

	thead {
		display: table-header-group;
	}

	tr,
	img {
		page-break-inside: avoid;
	}

	p,
	h2,
	h3 {
		orphans: 3;
		widows: 3;
	}

	h2,
	h3 {
		page-break-after: avoid;
	}
}
