@import "../../../../stylesheets/constants";
@import "../../../../stylesheets/colors";
@import "../../../../stylesheets/mixins";

.company-section {
	display: flex;
	justify-content: stretch;

	@include media($header-menu-breakpoint2) {
		display: block;
	}
	&__left {
		flex-basis: 50%;
		padding-right: 16px;
		border-right: 1px solid $light-gray;
		@include media($header-menu-breakpoint2) {
			border-right: none;
			border-bottom: 1px solid $light-gray;
			margin-bottom: 16px;
		}
	}
	&__right {
		box-sizing: border-box;
		padding: 0 16px;
		flex-basis: 50%;
		@include media($header-menu-breakpoint2) {
			padding: 0;
		}
	}

	&-title {
		font-size: em(32);
		margin-bottom: 16px;
	}
	&-icon {
		color: $main-red;
	}

	&-text {
		color: $secondary-text;
	}
}
