@import "../../../stylesheets/colors";
@import "../../../stylesheets/constants";
@import "../../../stylesheets/mixins";

.header-contacts {
	display: flex;
	flex-direction: column;
	justify-content: stretch;
	align-content: stretch;
	padding: 4px 0;
	&__contacts {
		display: flex;
		justify-content: flex-end;

		@include media($header-menu-breakpoint1) {
			display: none;
		}
	}

	&__link {
		font-size: 14px;
		display: inline-flex;
		padding: 0 8px;
		transition: color 125ms ease-in;
		&,
		&:active,
		&:visited {
			color: $primary_text;
			text-decoration: none;
		}
		&:hover {
			color: $main-red;
		}
	}

	&__icon {
		margin-right: 6px;
		color: $main-red;
		svg {
			width: 24px;
			height: 24px;
		}
	}
}

.header-tagline {
	&__text {
		font-size: 14px;
		color: $secondary-text;
	}
}
