@import "../../../stylesheets/colors";
@import "../../../stylesheets/constants";
@import "../../../stylesheets/mixins";

.mobile-menu {
	position: absolute;
	top: $header-height;
	left: 0;
	height: 0;
	right: 0;
	display: flex;
	flex-direction: column;
	transition: height 0.3s ease;
	z-index: 2;
	background: $page_bg;
	&_open {
		border-bottom: $secondary-text 1px solid;
		height: calc(100vh - #{$header-height});
		@include media($header-menu-breakpoint2) {
			height: calc(100vh - #{$header-height-mobile});
		}
	}

	&__inner {
		padding-top: 36px;
		overflow-y: auto;
	}
	@include media($header-menu-breakpoint2) {
		top: $header-height-mobile;
	}
}

.mobile-menu-item {
	opacity: 0;
	animation: 1s mobileMenu_appear forwards;

	&__link,
	&__link:visited {
		display: block;
		text-decoration: none;
		font-size: 16px;
		padding: 16px 0;
		margin: 0 20px;
		cursor: pointer;
		transition: color 0.2s ease-in-out;
		animation: 0.5s mobileMenu_slideIn forwards;
		&:hover {
			color: gray;
		}
		&:active,
		&:focus {
			text-decoration: none !important;
		}
	}

	&__line {
		width: 90%;
		height: 1px;
		background: gray;
		margin: 0 auto;
		transition: all 125ms ease-in-out;
		animation: 0.5s mobileMenu_shrink forwards;
		.mobile-menu-item_active & {
			height: 2px;
			background: $main-red;
		}
	}
}

@keyframes mobileMenu_appear {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}

@keyframes mobileMenu_slideIn {
	0% {
		transform: translateX(-2%);
	}
	100% {
		transform: translateX(0);
	}
}

@keyframes mobileMenu_shrink {
	0% {
		width: 95%;
	}
	100% {
		width: calc(100% - 40px);
	}
}

.mobile-menu-button {
	height: 32px;
	width: 32px;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	cursor: pointer;
	padding: 4px;

	&_line {
		height: 2px;
		width: 20px;
		transition: all 0.2s ease;
		background: $main-bg;

		&_top {
			transform: none;
			transform-origin: top left;
			margin-bottom: 5px;
		}
		&_middle {
			opacity: 1;
			transform: none;
		}
		&_bottom {
			transform: none;
			transform-origin: top left;
			margin-top: 5px;
		}
	}
	&--open {
		.mobile-menu-button_line_top {
			transform: rotate(45deg);
		}
		.mobile-menu-button_line_middle {
			opacity: 0;
			transform: translateX(-16px);
		}
		.mobile-menu-button_line_bottom {
			transform: translateX(-1px) rotate(-45deg);
		}
	}
}
