@import "../../../stylesheets/constants";
@import "../../../stylesheets/colors";
@import "../../../stylesheets/mixins";

.our-business {
}

.our-business__inner {
}

.our-business__list {
	width: 100%;
	display: grid;
	grid-gap: 16px;
	grid-template-columns: repeat(4, 1fr);
	@include media($header-menu-breakpoint1) {
		grid-template-columns: repeat(2, 1fr);
	}
	@include media($header-menu-breakpoint2) {
		grid-template-columns: repeat(1, 1fr);
	}
}

.our-business__item {
	display: flex;
}

.our-business-link {
	width: 100%;
	display: flex;
	flex-direction: column;
	&__img {
		padding-top: 80%;
		position: relative;
		img {
			width: 100%;
			position: absolute;
			top: 50%;
			z-index: 1;
			height: 100%;
			object-fit: cover;
			left: 50%;
			transform: translate(-50%, -50%);
		}
	}

	&__content {
		box-sizing: border-box;
		display: flex;
		flex-direction: column;
		padding: 16px 0;
		height: 100%;
	}

	&__title {
		text-align: center;

		margin-bottom: 8px;
		span {
			display: block;
		}
	}
	&-title {
		font-size: em(20);
		line-height: 100%;
		margin-bottom: 8px;

		&_red {
			font-weight: bold;
			color: $main-red;
		}
		&_gray {
			color: $primary_text;
		}
	}

	&-subtitle {
		font-size: em(14);
		flex-grow: 1;
	}
	&__link {
		margin-top: 16px;
	}
}
