@import "../../../../stylesheets/constants";
@import "../../../../stylesheets/colors";
@import "../../../../stylesheets/mixins";

.service-links {
}

.service-links__inner {
}

.service-links__list {
	width: 100%;
	display: grid;
	grid-gap: 16px;
	grid-template-columns: repeat(4, 1fr);
	@include media($header-menu-breakpoint2) {
		grid-template-columns: repeat(2, 1fr);
	}
}

.service-links__item {
	display: flex;
}

.service-link {
	width: 100%;
	display: flex;
	flex-direction: column;
	&__img {
		padding-top: 80%;
		position: relative;
		img {
			width: 80%;
			position: absolute;
			top: 50%;
			z-index: 1;
			height: 80%;
			left: 50%;
			transform: translate(-50%, -50%);
		}
	}

	&__content {
		box-sizing: border-box;
		display: flex;
		flex-direction: column;
		padding: 16px 0;
		height: 100%;
	}

	&__title {
		text-align: center;

		margin-bottom: 8px;
		span {
			display: block;
		}
	}
	&-title {
		font-size: em(24);
		line-height: 100%;
		text-transform: uppercase;
		margin: 0;

		&_red {
			font-weight: bold;
			color: $main-red;
		}
		&_gray {
			color: $primary_text;
		}
	}

	&-subtitle {
		text-align: center;

		font-size: em(14);
		line-height: 1.1;
		color: $secondary-text;
		flex-grow: 1;
	}
	&__link {
		margin-top: 16px;
	}
}
