@import "../../stylesheets/mixins";
@import "../../stylesheets/constants";
@import "../../stylesheets/colors";
$text-hover-color: #90939a;

.footer-contacts__item {
	display: flex;
	align-items: center;
	margin-bottom: 10px;

	&:last-child {
		margin-bottom: 0;
	}
}

.footer-contacts__logo {
	max-width: 218px;
	img {
		width: 100%;
	}
}

.footer-contacts__company {
	color: white;
	font-size: 20px;
	text-transform: uppercase;
	font-weight: 300;
	border-bottom: 1px solid #fff;
	text-align: center;

	margin-bottom: 16px;
}

.footer-contacts__icon {
	display: inline-flex;
	justify-content: center;
	align-items: center;
	width: 24px;
	height: 24px;
	margin-right: 8px;
	font-size: 14px;

	color: #282c37;

	background-color: #90939a;
	border-radius: 100%;

	svg {
		width: 1rem;
	}
	img {
		width: 100%;
	}
}

.footer-contacts__text {
	transition: all 250ms ease-in-out;
}

.footer-contacts__link,
.footer-contacts__link:visited {
	display: inline-flex;
	align-items: center;
	font-size: 1em;
	color: #f6f7fb;
	text-decoration: none;
	&:hover,
	&:active {
		.footer-contacts__text {
			transform: translateX(3px);
			color: $text-hover-color;
		}
	}
}

.footer-menu {
	display: flex;
	flex-wrap: wrap;
}

.footer-menu__col {
	margin-right: 80px;

	&:last-child {
		margin-right: 0;
	}
}

.footer-menu__item {
	margin-bottom: 10px;

	&:last-child {
		margin-bottom: 0;
	}
}

.footer-menu__link,
.footer-menu__link:visited {
	display: inline-block;
	font-size: 1em;
	color: #f6f7fb;
	text-decoration: none;
	transition: all 250ms ease-in-out;

	&:hover,
	&:active {
		transform: translateX(3px);
		color: $text-hover-color;
	}
}

.footer-col__title {
	font-size: 24px;
	font-weight: bold;
	color: #f6f7fb;
	margin-bottom: 16px;
	text-transform: uppercase;
}

.footer {
	padding: 0 45px;
	background-color: $footer-bg;
	user-select: none;
	@include media(420px) {
		padding: 0 20px;
	}
	@include media(380px) {
		padding: 0 15px;
	}
}

.footer-inner {
	display: flex;
	justify-content: space-between;
	max-width: 1180px;
	margin: 0 auto;
	padding: 0 0 24px;
	@include media($header-menu-breakpoint2) {
		display: block;
	}
}

.footer__menu {
	width: 45%;
	margin-right: 3.464%;
	flex-grow: 2;
	padding: 24px 0;
}

.footer__content {
	display: flex;
	justify-content: space-between;
	flex-grow: 1;
	margin-right: 20px;
}

.footer__contacts {
	flex-grow: 1;
	max-width: 240px;
}

.member-of {
	max-width: 260px;

	&__image {
		max-width: 120px;
	}
}

@include media(1120px) {
	.footer-menu > * {
		margin-bottom: 24px;
		& > *:last-child {
			margin-bottom: 0;
		}
	}

	.footer__menu {
		width: auto;
		margin-right: 0;
	}

	.footer__content {
		margin-bottom: 40px;
	}

	.footer__content {
		-webkit-justify-content: space-around;
		justify-content: space-around;
		width: auto;
		-ms-flex-pack: distribute;
	}
}

@include media($header-menu-breakpoint1) {
	.footer-menu {
		-webkit-justify-content: space-between;
		justify-content: space-between;
		-webkit-box-pack: justify;
		-ms-flex-pack: justify;
	}

	.footer__content {
		-webkit-justify-content: space-between;
		justify-content: space-between;
		-webkit-box-pack: justify;
		-ms-flex-pack: justify;
	}
}

@include media($header-menu-breakpoint2) {
	.footer-menu {
		display: block;
	}

	.footer-menu__col {
		margin-right: 0;
		margin-bottom: 24px;
	}

	.footer-menu__col:last-child {
		margin-bottom: 0;
	}

	.footer__menu {
		margin-bottom: 34px;
	}

	.footer__content {
		display: block;
	}
}
