@import "./colors";
@import "./constants";
@import "./mixins";

@font-face {
	font-family: "POE-Vetica-Bold";
	src: url("./fonts/poe_vetica_bold.otf") format("opentype");
	font-weight: bold;
}

.page {
	position: relative;
	display: flex;
	flex-direction: column;
	min-height: 100vh;

	background-color: $page_bg;
}

.page__content {
	position: relative;
	top: 0;
	left: 0;
	z-index: 1;

	flex-grow: 1;
	padding: $header-height 0 80px;
	transition: 125ms all ease-in-out;
	@include media($header-menu-breakpoint2) {
		padding-top: $header-height-mobile;
		padding-bottom: $header-height-mobile;
	}

	&_white {
		background: white;
	}
}

.page__section__inner {
	box-sizing: border-box;
	max-width: 1180px;
	padding: 24px;
	margin: 0 auto;

	@include media($header-menu-breakpoint1) {
		padding: 16px 0;
	}
}

.page__section {
	box-sizing: border-box;
	max-width: 1180px;
	padding: 24px;
	margin: 0 auto;

	@include media($header-menu-breakpoint1) {
		padding: 20px;
	}
	@include media($header-menu-breakpoint2) {
		padding: 16px;
	}

	&:empty {
		margin: 0 !important;
		padding: 0 !important;
	}

	&_no-padding {
		padding: 0;
	}

	&_bg {
		&_white {
			background-color: #fff;
		}

		&_gray {
			background-color: $main-bg;
		}
	}

	&_full_size {
		max-width: none;
	}

	&_rounded {
		border-radius: 4px;
	}
}

.page__wrapper {
	max-width: 1180px;
	margin: 0 auto;
	padding: 24px 24px 0;

	@include media($header-menu-breakpoint1) {
		padding: 24px 20px 0;
	}
	@include media($header-menu-breakpoint2) {
		padding: 24px 16px 0;
	}

	&_no-padding {
		padding: 0;
	}

	&_full-size {
		max-width: none;
		margin: 0;
		padding: 0;
	}
}

.simple-page {
	&-title {
		font-size: em(24);
		line-height: 100%;
		margin: 0;

		&_red {
			font-weight: bold;
			color: $main-red;
		}

		&_gray {
			color: $primary_text;
		}

		&_centered {
			text-align: center;
		}
	}

	&_style {
		&_uppercase {
			text-transform: uppercase;
		}

		&_bold {
			font-weight: bold;
		}
	}

	&-text {
		font-size: em(14);
		line-height: 1;
		color: $secondary-text;
	}
}

.link {
	&_reset {
		color: $primary_text;
		text-decoration: none;

		&:visited,
		&:hover,
		&:active {
			color: $primary_text;
		}

		&:hover {
			text-decoration: underline;
		}
	}

	&_as-text {
		color: $primary_text;
		text-decoration: none;

		&:visited,
		&:hover,
		&:active {
			color: $primary_text;
		}
	}

	&__text_white {
		color: white;

		&:visited,
		&:hover,
		&:active {
			color: white;
		}
	}
}
