@import "constants";
@import "mixins";
@import "colors";

.button {
	&_style {
		&_reset {
			outline: none;
			border: none;
			background: none;
			cursor: pointer;
		}
	}
}

.button {
	font-family: "Roboto", Arial, "Helvetica Neue", Helvetica, sans-serif;

	display: inline-block;
	box-sizing: border-box;
	margin-bottom: 0;
	padding: 0.8em 1em;
	line-height: 1;
	font-weight: 500;
	vertical-align: middle;
	text-align: center;
	text-decoration: none;
	white-space: nowrap;

	background-color: transparent;
	background-image: none;
	border-width: 1px;
	border-style: solid;
	border-color: transparent;
	border-radius: 0;
	cursor: pointer;

	transition-timing-function: ease-in-out;
	transition-duration: 125ms;
	transition-property: color, border-color, background-color, background-image;

	user-select: none;
	touch-action: manipulation;

	&:hover,
	&:focus {
		text-decoration: none;
	}

	&:active,
	&_active {
		background-image: none;
		outline: none;
		box-shadow: inset 0 3px 5px rgba(0, 0, 0, 0.125);
	}

	&_disabled,
	&[disabled],
	&_disabled:hover,
	&[disabled]:hover,
	&_disabled:focus,
	&[disabled]:focus,
	&_disabled:active,
	&[disabled]:active {
		color: #c0c4cd !important;

		background: #ebeef5 !important;
		outline: none;
		-webkit-box-shadow: none !important;
		box-shadow: none !important;
		opacity: 0.65;
	}
}

.button_block {
	display: block;
	width: 100%;
}

.button_size {
	&_extra-extra-small,
	&_xxs {
		font-size: 25%;
	}

	&_extra-small,
	&_xs {
		font-size: 50%;
	}

	&_small,
	&_s {
		padding: 0.7em 1em;
		font-size: 100%;
	}

	&_medium,
	&_m {
		padding: 0.94em 1em;
		font-size: 100%;
	}

	&_medium2,
	&_m2 {
		padding: 1.19em 1em;
		font-size: 100%;
	}

	&_large,
	&_l {
		padding: 1.217em 1em;

		font-size: 113%;
	}

	&_extra-large,
	&_xl {
		font-size: 150%;
	}

	&_extra-extra-large,
	&_xxl {
		font-size: 175%;
	}
}

.button_style {
	&_rounded {
		border-radius: 4px;
	}

	&_rounded_8 {
		border-radius: 10px;
	}
	&_rounded_10 {
		border-radius: 10px;
	}

	&_pill {
		border-radius: 100px;
	}

	&_flat {
		padding-top: em(4);
		padding-bottom: em(4);
	}

	&_solid {
		transition: background-color 125ms ease-in-out;
		&.button_color_red {
			color: #fff;
			background-color: $main-red;
		}

		&.button_color_red:focus,
		&.button_color_red:hover,
		&.button_color_red:active {
			background-color: lighten($main-red, 10);
		}

		&.button_color_black {
			color: #fff;
			background-color: $main-gray;
		}

		&.button_color_black:focus,
		&.button_color_black:hover,
		&.button_color_black:active {
			color: #fff;
			background-color: #282c37;
		}

		&.button_color_black:active {
			color: #90939a;
		}

		&.button_color_white {
			color: #282c37;

			background-color: #fff;
		}

		&.button_color_white:focus,
		&.button_color_white:hover,
		&.button_color_white:active {
			color: $primary_text;

			background-color: $main-bg;
		}

		&.button_color_white:active {
			color: #90939a;
		}
	}

	&_ghost {
		border-width: 1px;
		border-style: solid;

		&.button_color_red {
			color: #282c37;

			background-color: #fff;
			border-color: $main-red;
		}

		&.button_color_red:focus,
		&.button_color_red:hover,
		&.button_color_red:active {
			color: #282c37;

			background-color: $main-red;
			border-color: $main-red;
		}

		&.button_color_gray {
			color: #282c37;

			background-color: #fff;
			border-color: #f6f7fb;
		}

		&.button_color_gray:focus,
		&.button_color_gray:hover,
		&.button_color_gray:active {
			color: #282c37;

			background-color: #f6f7fb;
			border-color: #f6f7fb;
		}
	}
}

.button_no_outline {
	outline: none;
}

.button_style_thick-border {
	border-width: 2px;
}

.button_as_link {
	max-width: 200px;
	display: block;
	font-style: normal;
	font-weight: normal;
	font-size: 16px;
	line-height: 24px;
	text-align: center;
	text-decoration-line: underline;
	color: #282c37;
	cursor: pointer;
	margin-top: 24px;
}
